.section{padding:5px 15px 0px;position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:5px}@media screen and (min-width: 320px)and (max-width: 767px){.section{padding:5px 5px 0px}}.imageswrapper{margin-top:-10px;width:100%;border-top:50px solid #46a358;display:flex;flex-direction:column;justify-content:center;align-items:center}.images{margin-top:-30px;width:80%;display:flex;justify-content:space-between}@media screen and (min-width: 320px)and (max-width: 767px){.images{width:90%}}@media screen and (max-width: 424px){.images img{width:20%}}@media screen and (min-width: 425px){.images img{width:25%}}@media screen and (min-width: 500px){.images img{width:25%}}.mainPiсture{display:flex;justify-content:center;align-items:center;width:100%}@media screen and (min-width: 320px)and (max-width: 767px){.mainPiсture{border-radius:25px}}.mainPiсture img{padding-top:20px;background-color:#46a358;width:100%}@media screen and (min-width: 320px)and (max-width: 767px){.mainPiсture img{border-radius:25px 25px 0px 0px}}.image,.secondPlaneImage{width:28%;border:1px solid rgba(70,163,88,.1);border-radius:10px;padding:8px;background-color:#fff;cursor:pointer;transition:transform .5s}.secondPlaneImage{opacity:.6}.image:hover,.secondPlaneImage:hover{transform:scale(1.1)}